<template>
    <div class="pool">
        <h3>总瓜分金币池</h3>
        <div class="total">
            <div
                class="number"
                v-for="(v, i) in total"
                :key="i"
            >
                {{ v }}
            </div>
        </div>
        <footer>开奖倒计时： 23：59：59：00</footer>
    </div>
</template>

<script>
export default {
    props: {
        number: {
            type: Number,
            default: 0,
        },
    },
    computed: {
        total() {
            const arr = this.number.toString().split('').map(v => parseInt(v, 10));
            if (arr.length < 8) {
                arr.unshift(...new Array(8 - arr.length).fill(0));
            }
            return arr;
        },
    },
};
</script>

